export default {
	data() {
		return {
			indicatorDots: true,
			autoplay: true,
			interval: 3000,
			duration: 500,
			swiperArray:[]
		}
	},
	created() {
		this.getSwiperData()
	},
	methods:{
		// 请求服务器的轮播图数据
		getSwiperData(){
			uni.request({
				url:'https://www.linhua.club/ordering/wx/swiper',
				method:'GET',
				success: (res) => {
					// console.log(res.data.swiper)
					this.swiperArray=res.data.swiper
				}
			})
		}
	}
}
